html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

.content-box-component {
  box-sizing: content-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Lucida Console", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-style: normal;
  color: #788891; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden; }

.header {
  position: absolute;
  width: 100%; }

.header__content {
  margin: 0 auto;
  max-width: 1418px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 15px; }

.header__contacts-list {
  margin: 0;
  padding: 0; }

.logo {
  display: flex; }

.logo__wrapper {
  width: 52px;
  height: 52px; }

.logo__title {
  margin: 0;
  padding-left: 13px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  font-style: normal;
  color: #ffffff; }

.logo__title__sub {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  font-style: normal;
  color: #788891; }

.contacts-list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  list-style: none; }

.contacts-list__link {
  font-family: "PT Mono", "Lucida Console", monospace;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff; }

.contacts-list__item--phone {
  margin-left: 87px; }

.contacts-list__item--order {
  margin-left: 46px; }

.contacts-list__btn {
  padding: 12px 0;
  width: 180px;
  display: block;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 5px; }

.main-screen {
  padding-top: 328px;
  min-height: 878px;
  background-color: #1B2D37;
  background-image: url("../img/start-bg-desk-1x.png");
  background-repeat: no-repeat; }
